
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapGetters } from "vuex";
export default {
components: {
XHeader,
Group,
Cell
},
computed: {
...mapGetters(["personHelps"])
},
created() {



},
activated() {
let time = window.sessionStorage.getItem("help_time");
let start = window.sessionStorage.getItem("help_start");
let end = window.sessionStorage.getItem("help_end");
this.$store.dispatch("getPersonHelps", {
visitor_id: this.$route.params.personid,
time: time,
start: start,
end: end
});
},
watch: {
personHelps: function(val) {
this.helps = val;
}
},
data() {
return {
helps: []
};
},
methods: {}
};



